import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { createShortName } from '../../../utils/common'
import CustomSelect from '../../commanComponent/CustomSelect'
import { Input, Label } from '../../commanComponent'
import { validateSchema } from '../../../utils/ValidationHelper'
import { addUpdateQualificationSchema } from '../../../utils/ValidationSchema'
import { CustomButton, CustomError } from '../../globalComponents'
import { useCustomTranslation } from '../../../translation'
const CreateUpdateQualification = (props) => {
    const { addQualificationPending, updateQualificationPending } = useSelector(state => state?.qualification)
    const  {t} =useCustomTranslation()
    const {
        selectedQualificationData,
        openModel,
        handleOpenClose,
        handleCreateUpdateQualification,
        allQualifications
    } = props
    const [errors, setErrors] = useState({})
    const [createUpdateQualificationFormData, setCreateUpdateQualificationFormData] = useState({
        name: '',
        short_name: '',
        parent_id: null
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setCreateUpdateQualificationFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            if (name === 'name') {
                updatedState.short_name = createShortName(value);
                setErrors(prevErrors => ({
                    ...prevErrors,
                    ["short_name"]: undefined,
                }));
            }
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdateQualification = async () => {
        const validationResponse = await validateSchema(addUpdateQualificationSchema, createUpdateQualificationFormData);
        if (validationResponse.isValid) {
            const {
                parent,
                name,
                short_name,
            } = createUpdateQualificationFormData
            const params = {
                name,
                short_name,
                ["parent_id"]: parent?.value ||'',
            } 
            setErrors({})
            handleCreateUpdateQualification(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(() => {
        if (selectedQualificationData) {
            setCreateUpdateQualificationFormData({
                parent: { value: selectedQualificationData?.parent_id, label: selectedQualificationData?.parent_name },
                name: selectedQualificationData?.name,
                short_name: selectedQualificationData?.short_name,
            })
        }
    }, [selectedQualificationData])

    const resetForm = () => {
        setCreateUpdateQualificationFormData({
            parent: null,
            name: '',
            short_name: '',
        })
        setErrors({})
    }
    return (
        <>
        <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
            tabIndex={-1}
            style={{
                visibility: openModel ? 'visible' : 'hidden',
                width: "800px"
            }}
            id="interviewDetail"
        >
            <div className="offcanvas-header">
                <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{selectedQualificationData ? "Update" : "Add"} Qualification</h5>
                <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                    onClick={() => handleOpenClose()}
                />
            </div>

            <div className="offcanvas-body myRequest ">
                <div className="formPanel">
                    <div className="row">

                        <div className="col-md-3">
                            <div className="form-group mb-3">
                                <Label text={t("labels.name")} required={true}/>
                            </div>
                        </div>
                        <div className="col-md-9">
                            <div className="form-group mb-3">
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.qualificationName")}
                                    value={createUpdateQualificationFormData?.name}
                                    name="name"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.name && (
                                    <CustomError text={errors.name} />
                                )}
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-3">
                            <div className="form-group mb-3">
                            <Label text={t("labels.shortName")} required={true}/>
                            </div>
                        </div>
                        <div className="col-md-9">
                            <div className="form-group mb-3">
                                <input type="text" className="form-control fs-7"
                                    name='short_name'
                                    placeholder={t("placeholders.shortName")}
                                    value={createUpdateQualificationFormData?.short_name}
                                    onChange={(e) => handleFromChange(e)}

                                />
                                {errors?.short_name && (
                                    <CustomError text={errors.short_name} />
                                )}
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-3">
                            <div className="form-group mb-3">
                            <Label text={t("labels.parentQualification")}/>
                            </div>
                        </div>
                        <div className="col-md-9">
                            <div className="form-group mb-3">
                                <CustomSelect
                                    data={allQualifications}
                                    placeholder={t("placeholders.parentQualification")}
                                    value={createUpdateQualificationFormData?.parent || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "parent")}
                                    name="parent"
                                />

                            </div>
                        </div>
                    </div>


                </div>
            </div>
            <div className="offcanvas-footer">
                <div className="d-flex align-items-center justify-content-end gap-3">
                    <CustomButton
                        text={selectedQualificationData ? t("btns.update") : t("btns.submit")}
                        classNames={"btn btn-org-gradient"}
                        handleClick={() => submitCreateUpdateQualification()}
                        isLoading={selectedQualificationData ? updateQualificationPending : addQualificationPending}
                    />
                </div>
            </div>
        </div>
        {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateUpdateQualification
