import React, { useEffect, useMemo, useState } from 'react'
import { CustomTable } from '../../reuseableComponents'
import { getBadgeColor } from '../../../utils/common'
import { CustomButton } from '../../globalComponents'
import moment from 'moment'
import { useCustomTranslation } from '../../../translation'
import { Icon } from '../../commanComponent'

const QualificationTable = (props) => {
    const { t } = useCustomTranslation()
    const {
        isLoading,
        setSelectedQualificationData,
        toggleCreateUpdateQualificationModal,
        toggleQualificationDeleteModal,
        currentPage,
        setCurrentPage,
        allQualificationsByParentId
    } = props
    const [qualificationList, setQualificationList] = useState('')


    const handleUpdateQualification = (data) => {
        toggleCreateUpdateQualificationModal(data)
    }

    useEffect(() => {
        setQualificationList(allQualificationsByParentId)
    }, [allQualificationsByParentId])


    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        is_active: item => (
            <span
                className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),
        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <span className="dropdown-item"
                            onClick={() => handleUpdateQualification(item)}
                        >
                            <Icon className={"bi bi-pencil-square me-1"} />Update</span>
                    </li>
                    <li>
                        <span className="dropdown-item"
                            onClick={() => toggleQualificationDeleteModal(item._id)}
                        >
                            <Icon className={"bi bi-trash me-1"} />Delete</span>
                    </li>
                </ul>
            </div>

        )
    }), [setSelectedQualificationData, toggleCreateUpdateQualificationModal]);

    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'name', label: 'Qualification Name' },
        { key: 'short_name', label: 'Short Name' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label: 'Action' }
    ];

    return (
        <>
            <CustomTable
                data={qualificationList?.data}
                fields={fields}
                isLoading={isLoading}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={qualificationList?.totalPages}
                tableName={t("headings.qualification")}
                // onSearch={handleSearch}
                scopedSlots={scopedSlots}
            />
        </>
    )
}

export default QualificationTable
