import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import { qualificationActions } from '../../../core/resource/actions'
import { pagination } from '../../../utils/Constants.config'
import CreateUpdateQualification from './CreateUpdateQualification'
import { CustomButton } from '../../globalComponents'
import QualificationTable from './qualificationTable'
import { useCustomTranslation } from '../../../translation'
const Qualification = () => {
    const dispatch = useDispatch()
    const { t } = useCustomTranslation()
    const {
        addQualificationPending,
        addQualificationRes,
        addQualificationError,

        getAllQualificationPending,
        getAllQualificationRes,
        getAllQualificationError,

        getAllQualificationsByParentIdPending,
        getAllQualificationsByParentIdRes,
        getAllQualificationsByParentIdError,

        updateQualificationPending,
        updateQualificationRes,
        updateQualificationError,

        deleteQualificationPending,
        deleteQualificationRes,
        deleteQualificationError,

    } = useSelector(state => state.qualification)

    const [isOpenCreateUpdateQualification, setIsOpenCreateUpdateQualification] = useState(false)
    const [selectedQualificationData, setSelectedQualificationData] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [allQualifications, setAllQualifications] = useState([])
    const [isShowDeleteQualificationModal, setIsShowDeleteQualificationModal] = useState(false)
    const [deleteQualificationId, setDeleteQualificationId] = useState('')
    const [allQualificationsByParentId, setAllQualificationsByParentId] = useState('')

    /* Create Update Qualification */
    const toggleCreateUpdateQualificationModal = (data) => {
        if (!data) {
            setSelectedQualificationData('')
        } else {
            setSelectedQualificationData(data)
        }
        setIsOpenCreateUpdateQualification(!isOpenCreateUpdateQualification)
    }

    const handleCreateUpdateQualification = (params) => {
        if (selectedQualificationData) {
            params['_id'] = selectedQualificationData?._id
            dispatch(qualificationActions.updateQualificationRequest(params));
        }
        else {
            dispatch(qualificationActions.addQualificationRequest(params));
        }
    }

    useEffect(() => {
        if (!addQualificationPending) {
            if (addQualificationError) {
                dispatch(qualificationActions.handleClearQualificationStore("HANDLE_CLEAR_ADD_QUALIFICATION_STORE"))
                return;
            }
            if (addQualificationRes) {
                if (addQualificationRes?.code === 200) {
                    dispatch(qualificationActions.handleClearQualificationStore("HANDLE_CLEAR_ADD_QUALIFICATION_STORE"))
                    getAllQualificationsByParentId()
                    getAllQualifications()
                    setIsOpenCreateUpdateQualification(!isOpenCreateUpdateQualification)
                }
            }
        }
    }, [
        addQualificationRes,
        addQualificationError,
        addQualificationPending
    ]);


    useEffect(() => {
        if (!updateQualificationPending) {
            if (updateQualificationError) {
                dispatch(qualificationActions.handleClearQualificationStore("HANDLE_CLEAR_UPDATE_QUALIFICATION_STORE"))
                return;
            }
            if (updateQualificationRes) {
                if (updateQualificationRes?.code === 200) {
                    dispatch(qualificationActions.handleClearQualificationStore("HANDLE_CLEAR_UPDATE_QUALIFICATION_STORE"))
                    getAllQualificationsByParentId()
                    getAllQualifications()
                    setIsOpenCreateUpdateQualification(!isOpenCreateUpdateQualification)
                }
            }
        }
    }, [
        updateQualificationRes,
        updateQualificationError,
        updateQualificationPending
    ]);

    /* Get All Qualifications By Parent Id */
    useEffect(() => {
        getAllQualificationsByParentId()
    }, [currentPage])
    const getAllQualificationsByParentId = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(qualificationActions.getAllQualificationsByParentIdRequest(params));
    }

    useEffect(() => {
        if (!getAllQualificationsByParentIdPending) {
            if (getAllQualificationsByParentIdError) {
                return;
            }
            if (getAllQualificationsByParentIdRes) {
                if (getAllQualificationsByParentIdRes?.code === 200) {
                    setAllQualificationsByParentId(getAllQualificationsByParentIdRes?.data)
                }
            }
        }
    }, [
        getAllQualificationsByParentIdRes,
        getAllQualificationsByParentIdError,
        getAllQualificationsByParentIdPending
    ]);

    /* Get All Qualification */
    useEffect(() => {
        getAllQualifications()
    }, [])
    const getAllQualifications = () => {
        dispatch(qualificationActions.getAllQualificationRequest());

    }

    useEffect(() => {
        if (!getAllQualificationPending) {
            if (getAllQualificationError) {
                return;
            }
            if (getAllQualificationRes) {
                if (getAllQualificationRes?.code === 200) {
                    setAllQualifications(getAllQualificationRes?.data)
                }
            }
        }
    }, [
        getAllQualificationRes,
        getAllQualificationError,
        getAllQualificationPending
    ]);


    /* Delete Qualification */
    const toggleQualificationDeleteModal = (id) => {
        if (id) {
            setDeleteQualificationId(id)
        }
        setIsShowDeleteQualificationModal(!isShowDeleteQualificationModal)
    }

    const handleDeleteQualification = (type) => {
        if (type) {
            const params = {
                _id: deleteQualificationId
            }
            dispatch(qualificationActions?.deleteQualificationRequest(params))
        } else {
            toggleQualificationDeleteModal()
        }

    }

    useEffect(() => {
        if (!deleteQualificationPending) {
            if (deleteQualificationError) {
                dispatch(qualificationActions.handleClearQualificationStore("HANDLE_CLEAR_DELETE_QUALIFICATION_STORE"))
                return;
            }
            if (deleteQualificationRes) {
                if (deleteQualificationRes?.code === 200) {
                    toggleQualificationDeleteModal()  
                    
                    if (currentPage > 1 && allQualificationsByParentId?.data?.length === 1) {
                        setCurrentPage((pre => pre -= 1))
                      } else {
                        getAllQualificationsByParentId()
                      }
                      getAllQualifications()
                    dispatch(qualificationActions.handleClearQualificationStore("HANDLE_CLEAR_DELETE_QUALIFICATION_STORE"))
                }
            }
        }
    }, [
        deleteQualificationRes,
        deleteQualificationError,
        deleteQualificationPending
    ]);


    return (
        <>
            <div className="d-flex justify-content-end ">
                <CustomButton
                    classNames={"btn btn-primary gradientBg"}
                    handleClick={() => toggleCreateUpdateQualificationModal()}
                    text={t("btns.createQualification")}
                />
            </div>
            <CreateUpdateQualification
                openModel={isOpenCreateUpdateQualification}
                handleOpenClose={toggleCreateUpdateQualificationModal}
                handleCreateUpdateQualification={handleCreateUpdateQualification}
                selectedQualificationData={selectedQualificationData}
                allQualifications={allQualifications}
            />
            <QualificationTable
                setSelectedQualificationData={setSelectedQualificationData}
                toggleCreateUpdateQualificationModal={toggleCreateUpdateQualificationModal}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                allQualifications={allQualifications}
                toggleQualificationDeleteModal={toggleQualificationDeleteModal}
                allQualificationsByParentId={allQualificationsByParentId}
            />

            {isShowDeleteQualificationModal && (
                <ApproveRejectModal
                    onApproveReject={handleDeleteQualification}
                    isShow={isShowDeleteQualificationModal}
                    desc={t("alerts.deletePopup.desc").replace('$1',"Qualification")}
                    btnRightLoading={deleteQualificationPending}
                    btnTextRight={t("btns.delete")}
                    btnTextLeft={t("btns.cancel")}

                />)
            }
        </>
    )
}

export default Qualification
